import bike from '../images/bike.png'

const LocationSearch = (props) => {
  return (
    <section className="location-search-container">
      <div className="location-container">
        <figure className="bike-container">
          <img src={bike} draggable="false" />
        </figure>

        <div>
          <p>Now</p>
          <h1>Walthamstow London</h1>
        </div>
      </div>

      <div className="button-container">
        <button>Dine in</button>
        <button>Delivery</button>
      </div>

      <div className="search-container">
        <input type="text" placeholder="Dishes, restaurants or cuisines" />

        <figure className="filter-button">
          <svg
            height="24"
            width="24"
            viewBox="0 0 24 24"
            className="ccl-0f24ac4b87ce1f67 ccl-ed34b65f78f16205 ccl-c738ab1fde928049"
          >
            <path d="M11 11H21V13H11V15H7V13H3V11H7V9H11V11ZM13 17V15H17V21H13V19H3V17H13ZM14 5V3H18V5H21V7H18V9H14V7H3V5H14ZM15 8H17V4H15V8ZM17 19V17H21V19H17ZM14 20H16V16H14V20ZM8 14H10V10H8V14Z"></path>
          </svg>
        </figure>
      </div>
    </section>
  );
};

export default LocationSearch;
