import LocationSearch from "./LocationSearch";
import CategoryCard from "./CategoryCard";
import RestaurantCard from "./RestaurantCard";

import nandos from '../images/nandos.png'
import enish from '../images/enish.jpeg'
import hakkasan from '../images/hakkasan.jpeg'
import fatBurgers from '../images/fatburgers.jpeg'
import franzos from '../images/franzos.jpeg'
import orfords from '../images/orfords.jpeg'

import periperi from '../images/periperibg.png'
import nigerian from '../images/nigerianbg.webp'
import thai from '../images/thaibg.png'
import burger from '../images/burgerbg.png'

const HomePage = () => {
  return (
    <div className="main-container">
      <LocationSearch />

      <div className="category-container">
        <CategoryCard backgroundImage={periperi} foodType='Peri Peri' />
        <CategoryCard backgroundImage={nigerian} foodType='Nigerian' />
        <CategoryCard backgroundImage={thai} foodType='Thai'/>
        <CategoryCard backgroundImage={burger} foodType='Burgers'/>
      </div>

      <h1 className='heading'>Visit again</h1>
      <RestaurantCard backgroundImage={nandos} notice='Long Queue' rating='4.4 Very Good' category1='Chicken' category2='Burgers'/>

      <h1 className='heading'>Explore</h1>
      <RestaurantCard backgroundImage={nandos} name='Nandos' notice='Long Queue' rating='4.4 Very Good' category1='Chicken' category2='Burgers'/>
      <RestaurantCard backgroundImage={hakkasan} name='Hakkasan' notice='Long Queue' rating='5.0 Excellent' category1='Chicken' category2='African'/>
      <RestaurantCard backgroundImage={enish} name='Enish' notice='Long Queue' rating='4.4 Very Good' category1='Chicken' category2='Burgers'/>
      <RestaurantCard backgroundImage={fatBurgers} name='Fat Burgers' notice='Long Queue' rating='4.4 Very Good' category1='Chicken' category2='Burgers'/>
      <RestaurantCard backgroundImage={franzos} name='Franzos' notice='Long Queue' rating='4.4 Very Good' category1='Chicken' category2='Burgers'/>
      <RestaurantCard backgroundImage={orfords} name="Orford's Fish Chips" notice='Long Queue' rating='4.4 Very Good' category1='Chicken' category2='Burgers'/>

    </div>
  );
};

export default HomePage;
